body {
  font-size: 1rem;
}

#wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

header .header-wrapper {
  height: 5rem;
  display: flex;
  align-items: center;
  border-bottom: 0.0625rem solid hsla(220deg, 14%, 75%, 0.4);
}

main {
  height: 100%;
  flex-grow: 1;
}

footer .footer-wrapper {
  padding: 1.25rem 0.625rem;
  border-top: 0.0625rem solid hsl(220deg, 14%, 75%);
  font-size: 0.75rem;
}

.header-wrapper,
.content-wrapper,
.footer-wrapper {
  width: 100%;
  max-width: 90rem;
  margin: 0 auto;
  position: relative;
}